import React, { Component } from 'react';
import {Modal, Button,} from 'react-bootstrap'
import {
  Col,
  FormGroup,
  Input,
  Select,
  Label,
  Row,
} from 'reactstrap';
//import Spinner from '../../Loaders/Spinner'
import './Accounts.scss'
import Feedback from 'react-bootstrap/Feedback';
import Server_connections from '../../constants/constants';

class AddWithdrawRequests extends Component {
    constructor(props) {
      super(props);
      this.state = {
        landlord_code: '',
        property_name: '',
        amount: '',
        commission: '',
        destination: '',
        message: '',
        isLoaded: false,
      }
      this.handleUpdateItem = this.handleUpdateItem.bind(this);
      this.onChange = this.onChange.bind(this);
    }

    onChange(e){
      this.setState({[e.target.name]: e.target.value});
      //console.log({[e.target.name]: e.target.value});
    }

    handleUpdateItem(e){
      e.preventDefault();

      const addNewWithdrawRequests = {
        landlord_code: this.state.landlord_code,
        property_name: this.state.property_name,
        amount: this.state.amount,
        commission: this.state.commission,
        destination: this.state.destination
      };

      fetch(Server_connections.ADD_WITHDRAW_REQUESTS_URL,{
        method: 'POST',
        headers:{
          'Accept': 'application/json',
          'Content-Type': 'application/json'
        },
        body:JSON.stringify(addNewWithdrawRequests)
      })
      .then(res => res.json())
      .then((result) =>{
        this.setState({message : result.message, isLoaded : true})
        console.log([this.state.message])
      },
      (error) => {
        this.setState({message : 'Failed.. Try again!!'})
      });
    }

    render() {
      //var { counties, districts, amounts} = this.state;
        return (
            <Modal
              {...this.props}
              size="lg"
              aria-labelledby="contained-modal-title-vcenter"
              centered
              id="exampleModal"
            >
              <Modal.Header closeButton>
                <Modal.Title id="contained-modal-title-vcenter">
                  <div className="modal-title">Add New Withdraws | <span className='modal-alert'>{this.state.message }</span></div>
                </Modal.Title>
              </Modal.Header>
              <Modal.Body>
                <FormGroup row className="my-0">
                  <Col xs="6">
                    <FormGroup>
                      <Label htmlFor="landlord_code" className="lable">Landlord Code</Label>
                      <Input type="text" name="landlord_code" placeholder="Business Name" value ={this.state.landlord_code} onChange={this.onChange}/>
                    </FormGroup>
                  </Col>
                  <Col xs="6">
                    <FormGroup>
                        <Label htmlFor="property_name" className="lable">Property Name</Label>
                        <Input type="text" name="property_name" placeholder='Property Name' value={this.state.property_name} onChange={this.onChange}/>
                    </FormGroup>
                  </Col>
                </FormGroup>
                <FormGroup row className="my-0">
                  <Col xs="6">
                    <FormGroup>
                      <Label htmlFor="amount" className="lable">Amount Withdrawn</Label>
                      <Input type="text" name="amount" placeholder="Amount" value={this.state.amount} onChange={this.onChange}/>
                    </FormGroup>
                  </Col>
                  <Col xs="6">
                    <FormGroup>
                      <Label htmlFor="commission" className="lable">Commission Paid</Label>
                      <Input type="text" name="commission" placeholder="Commision" value={this.state.commission} onChange={this.onChange}/>
                    </FormGroup>
                  </Col>
                </FormGroup>
                <FormGroup row className="my-0">
                  <Col xs="8">
                    <FormGroup>
                      <Label htmlFor="destination" className="lable">Destination</Label>
                      <Input type="text" name="destination" placeholder="Bank account or moneny money number" value={this.state.destination} onChange={this.onChange}/>
                    </FormGroup>
                  </Col>
                </FormGroup>
              </Modal.Body>
              <Modal.Footer>
                <div className="d-inline">
                  <Button block color="danger" size="sm" className="btn-pill bg-danger" onClick={this.props.onHide}>Cancle</Button>
                </div>
                <div className="d-inline">
                  <Button block color="success" size="sm" className="btn-pill bg-success" onClick={this.handleUpdateItem}>Save</Button>
                </div>
              </Modal.Footer>
            </Modal>
          );
    }
}

export default AddWithdrawRequests;