import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import classNames from 'classnames';
import { Row, Col,Table, Button } from 'reactstrap'
import { rgbToHex } from '@coreui/coreui/dist/js/coreui-utilities'
import jsPDF from "jspdf";
import "jspdf-autotable";  
import AddWithdrawRequests from './AddWithdraws'
import './Accounts.scss'
import logo from '../../assets/img/brand/mispace3.png'
import Server_connections from '../../constants/constants';
import UserInfo from '../../constants/credentials'

var date = new Date();
var monthNames = ["January", "February", "March", "April", "May", "June",
  "July", "August", "September", "October", "November", "December"
];

class ThemeView extends Component {
  constructor(props) {
    super(props);

    this.state = {
      bgColor: 'rgb(255, 255, 255)'
    }
  }

  componentDidMount () {
    const elem = ReactDOM.findDOMNode(this).parentNode.firstChild
    const color = window.getComputedStyle(elem).getPropertyValue('background-color')
    this.setState({
      bgColor: color || this.state.bgColor
    })
  }

  render() {
      
    return (
      <table className="w-100">
        <tbody>
        <tr>
          <td className="text-muted">HEX:</td>
          <td className="font-weight-bold">{ rgbToHex(this.state.bgColor) }</td>
        </tr>
        <tr>
          <td className="text-muted">RGB:</td>
          <td className="font-weight-bold">{ this.state.bgColor }</td>
        </tr>
        </tbody>
      </table>
    )
  }
}

class ThemeColor extends Component {
  // constructor(props) {
  //   super(props);
  // }
  render() {

    // const { className, children, ...attributes } = this.props
    const { className, children } = this.props

    const classes = classNames(className, 'theme-color w-75 rounded mb-3')

    return (
      <Col xl="2" md="4" sm="6" xs="12" className="mb-4">
        <div className={classes} style={{paddingTop: '75%'}}></div>
        {children}
        <ThemeView/>
      </Col>
    )
  }
}

class WithdrawRequests extends Component {
  constructor(props) {
    super(props);
    this.state = {
      withdraws: [],
      isLoaded: false,
      searchItem: '',
      icon: logo,
      addModalShow: false
    }
    this.handleExcelExport = this.handleExcelExport.bind(this);
    this.handleRefreshButton = this.handleRefreshButton.bind(this);
  }

  handleExcelExport(e){
    e.preventDefault();

    var doc = new jsPDF({
        unit: 'in',
         format: 'a4' 
    })

    const title = `Withdraw Requests Report For All Registered Landlords`;
    const headers = [["Code", "Name", "Amount Witdrawn", "Commission", "Month"]];

    const data = this.state.withdraws.map(elt => [elt.landlord_code, elt.property_name, elt.amount, elt.commission, elt.month]);

    let content = {
      startY: 2.0,
      head: headers,
      body: data,
    };

    doc.addImage(this.state.icon, 'png', 2.9, 0.5, 2.2, 0.5)
    doc.setFontSize(14)
    doc.text(title, 1.7, 1.4)
    doc.text(``, 0.5, 1.7).setFontSize(5)
    doc.autoTable(content);

    doc.save(`Account_balances.pdf`)
  }

  handleSearchInput = (e) => {
    this.setState({ searchItem: e.target.value})
  }

  handleRefreshButton(e){
    e.preventDefault();
    window.location.reload(false);
  }

  componentDidMount(){
    fetch(Server_connections.GET_ALL_WITHDRAWS_URL + `/${UserInfo.UserCode}`)
    .then(res => res.json())
    .then(json => {
      this.setState({
        withdraws:  json.withdraws,
        isLoaded: true
      })
    });

    fetch(Server_connections.LANDLORD_BOOKINGS_VALUE_URL + `/${UserInfo.UserCode}`)
    .then(res => res.json())
    .then(json => {
    this.setState({
      money:  json.money,
      isLoaded: true
    })
    console.log([this.state.money])
    });
  }
  render() {
    var { isLoaded, withdraws } = this.state;

    let addModalClose = () => this.setState({addModalShow : false});

    let filteredItem = withdraws.filter((item) => {
      return item.month.toLowerCase().includes(this.state.searchItem.toLowerCase());
    });
    return (
      <div className="animated fadeIn">
        <div className="card">
          <div className="card-header">
          {/* <Button className="btn-pill btn-success" size="sm" onClick = {() => this.setState({addModalShow : true})}>
              <strong>Add Withdraws</strong>
          </Button> */}
          <Button className="btn-pill btn-success btn-space" size="sm" onClick = {this.handleRefreshButton}>
              <strong>Refresh</strong>
          </Button>
          {/* <Button className="btn-pill btn-success btn-space" size="sm" onClick = {this.handleExcelExport}>
              <strong>Export To PDF</strong>
          </Button> */}
          </div>
          <div className="card-body">
          <div>
              <div className="">
                  <strong> These are the live withdraw request logs that are being processed right now.</strong>
                  <form className="form-inline mr-auto formRight active-pink-3 active-pink-4">
                      <input className="form-control mr-sm-2" type="text" placeholder="Search by Month" aria-label="Search" 
                      onChange={this.handleSearchInput}/>
                      {/* <button className="btn-pill btn-success" type="submit">Search</button> */}
                      <i class="icon-search" aria-hidden="true"></i>
                  </form>
              </div><hr/>
              <div className= 'parentDiv2'>
              <Table hover responsive className="table-outline mb-0 d-none d-sm-table">
                <thead className="thead-light">
                <tr>
                  <th>Landlord Ref</th>
                  <th>Name</th>
                  <th>Amount Received</th>
                  <th>Commision Paid</th>
                  <th>Destination</th>
                  <th>Month</th>
                  <th>Year</th>
                  <th>Updated at</th>
                </tr>
                </thead>
                <tbody>
                {filteredItem.map((item, index) => (
                <tr key={ index }>
                  <td>
                    <div>{item.landlord_code}</div>
                  </td>
                  <td>
                    <div>{item.property_name}</div>
                  </td>
                  <td className="">
                    <div>{item.amount}</div>
                  </td>
                  <td>
                    <div>{item.commission}</div>
                  </td>
                  <td className="">
                    <div>{item.destination}</div>
                  </td>
                  <td>
                    <div>{item.month}</div>
                  </td>
                  <td>
                    <div>{item.year}</div>
                  </td>
                  <td>
                <div>{item.created_at}</div>
                  </td>
                </tr>
                ))}
                </tbody>
              </Table>
          </div>
          </div>
          </div>
        </div>
        <AddWithdrawRequests
          show = {this.state.addModalShow}
          onHide = {addModalClose}/>
      </div>
    );
  }
}

export default WithdrawRequests;
